h1 {
    font-size: 48px;
    line-height: 1.2;
    font-weight: 700;
    color: var(--color-b);
    @media (max-width: $l) {
        @include calc(font-size, 28, 48);
    }
}
h2 {
    font-size: 36px;
    line-height: 1.2;
    font-weight: 700;
    color: var(--color-b);
    @media (max-width: $l) {
        @include calc(font-size, 20, 36);
    }
}
h4 {
   
    font-size: 24px;
    line-height: 1.2;
    font-weight: 700;
    color: var(--color-b);
    @media (max-width: $l) {
        @include calc(font-size, 18, 24);
    }
}
.title-center {
    text-align: center;
}
.title-line {
    position: relative;
    padding-bottom: 24px;
    @media (max-width: $x) {
        padding-bottom: 10px;
    }
    &::before {
        position: absolute;
        content: "";
        width: 484px;
        height: 3px;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);
        background-color: #03def2;
        // background-image: linear-gradient(90.0deg, #5f8fc6 0%, #0497cd 100%);
        background-image: linear-gradient( 45deg, rgb(95,143,198) 0%, rgb(4,151,205) 0%, rgb(107,69,233) 0%, rgb(190,171,167) 0%, rgb(116,154,250) 100%);
        @media (max-width: $l) {
            @include calc(width, 150, 484);
        }
    }
}
