.nav {
    position: relative;
    &::before {
        position: absolute;
        content: "";
        background-image: linear-gradient(90deg, #7cacec 0%, #367eef 50%);
        opacity: 0.52;
        width: 100%;
        height: 100%;
    }
}

.nav__list {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
   user-select: none;
}
.mobile-menu {
}
.nav__item {
    cursor: pointer;
    position: relative;
    transition: 0.3s ease-in;
    @media (min-width: 767px) {
        &:hover {
            &::before {
                position: absolute;
                content: "";
                width: 100%;
                height: 3px;
                background-color: #03def2;
                background-image: linear-gradient(
                    90deg,
                    #a6c1df 0%,
                    #74c5e3 100%
                );
                left: 0;
                bottom: -3px;
            }
        }
    }
}
.nav__link {
    position: relative;
    display: grid;
    padding: 14px 10px 12px;
    justify-items: center;
    font-family: var(--font-m);
    font-size: 18px;
    font-weight: 800;
    color: #ffffff;
    transition: 0.3s ease-in;
    @media (max-width: $m) {
        display: block;
    }
    @media (min-width: 767px) {
        &:hover {
            background-image: linear-gradient(90deg, #a6c1df 0%, #74c5e3 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-background-clip: text;
            -moz-text-fill-color: transparent;
        }
    }
   
    &::after {
        position: absolute;
        content: "";
        width: 1px;
        height: 13px;
        background-color: #ffffff;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        @media (max-width: $m) {
            display: none;
        }
    }
    &:nth-child(1) {
        &::before {
            position: absolute;
            content: "";
            width: 1px;
            height: 13px;
            background-color: #ffffff;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            @media (max-width: $m) {
                display: none;
            }
        }
    }
}
.nav__item--catalog {
    transition: 0.3s ease-in;
    @media (min-width: 767px) {
        &:hover {
            .nav__sub-list {
                display: block;
            }
        }
    }
}
.nav__sub-list {
    display: none;
    position: absolute;
    top: 50px;
    width: 100%;
    background-color: #ffffff;
    border: 1px solid rgba(88, 88, 92, 0.48);
    &.nav__sub-list--active {
        display: block;
        z-index: 1;
    }
}
.nav__sub-item {
    display: flex;
    align-items: center;
    // justify-content: center;
    transition: 0.3s ease-in;
    &:hover {
        background-color: var(--color-b);
        .nav__sub-link {
            color: #ffffff;
        }
    }
    &:not(:last-child) {
        .nav__sub-link {
            &::before {
                position: absolute;
                content: "";
                width: 157px;
                height: 1px;
                opacity: 0.5;
                background-color: #58585c;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                @media (max-width: $l) {
                    width: 100%;
                }
            }
        }
    }
}
.nav__sub-link {
    position: relative;
    width: max-content;
    font-family: var(--font-o);
    font-size: 16px;
    font-weight: 400;
    color: #58585c;
    padding: 10px 45px;
    @media (max-width: $l) {
        padding: 10px 5px;
    }
    @media (max-width: $l) {
        width: 100%;
    }
}
.nav__item--hidden {
    display: none;
    @media (max-width: $m) {
        display: block;
    }
}
