.nav__list.mobile-menu { 
    @media (max-width: $m) {
        z-index: 2;  
        position: fixed;
        top: 0;
        right: -100%;
        opacity: 0;
        display: flex;
        flex-direction: column;
        justify-content: start;
        // align-items: center;
        padding-top: 20px;
        padding-left: 50px;
        background-color: #a8a9c6;
        width: 320px;
        height: 100vh;
        transition: all 0.5s ease-in;  
        &.active {
            right: 0;
            opacity: 1;
        }
    }
  
    
}
