.sertificate {
    // @extend .bg;
    padding-bottom: 54px;
    @media (max-width: $x) {
        padding-bottom: 30px;
    }
}
.small-container {
    margin-right: auto;
    margin-left: auto;
    max-width: 1125px;
    @media (max-width: $l) {
        max-width: 100%;
    }
}
.sertificate__title {
    margin-bottom: 50px;
    @media (max-width: $x) {
        margin-bottom: 30px;
    }
}

.sertificate__desc {
    max-width: 1010px;
    position: relative;
    margin-bottom: 55px;
    line-height: 1.333;
    @media (max-width: $x) {
        margin-bottom: 30px;
    }
}
.sertificate__slider {
    position: relative;
}
.sertificate__swiper {
    @media (max-width: 1300px) {
        // padding-left: 70px;
        // padding-right: 70px;
        // margin-left: -70px;
        // margin-right: -70px;
        margin-left: auto;
        margin-right: auto;
        // max-width: 1000px;
        width: 85%;
    }
    @media (max-width: $xx) {
        width: 75%;
    }
    @media (max-width: $x) {
        width: 70%;
    }
}

.sertificate__slide {
    position: relative;
    display: block;
    transition: opacity 0.3s ease-in;
    &::before {
        opacity: 0;
        transition: opacity 0.3s ease-in;
    }
    &:hover {
        &::before {
            position: absolute;
            content: "";
            background-image: linear-gradient(
                0deg,
                rgb(95, 143, 198) 0%,
                rgb(4, 151, 205) 0%,
                rgb(107, 69, 233) 0%,
                rgb(190, 171, 167) 0%,
                rgb(116, 154, 250) 100%
            );
            width: 100%;
            height: 100%;
            opacity: 0.5;
        }
    }
}
.sertificate__slide-img {
    &:hover {
        background-image: linear-gradient(
            0deg,
            rgb(95, 143, 198) 0%,
            rgb(4, 151, 205) 0%,
            rgb(107, 69, 233) 0%,
            rgb(190, 171, 167) 0%,
            rgb(116, 154, 250) 100%
        );
    }
}
.seriticate__controls {
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
}

.seriticate-prev {
    left: -85px;
    @media (max-width: 1300px) {
        left: -15px;
    }
}

.seriticate-next {
    right: -85px;
    @media (max-width: 1300px) {
        right: -15px;
    }
}
