.benefits {
    padding-bottom: 30px;
    position: relative;
    .title-line::before {
        width: 600px;
        @media (max-width: $l) {
            @include calc(width, 250, 600);
        }
    }
}

.bentefits__title {
    margin-bottom: 60px;
    @media (max-width: $x) {
        margin-bottom: 30px;
    }
}

.benefits__container {
    display: flex;   
    flex-wrap: wrap;
    @media (max-width: $mn) {
        row-gap: 10px;
    }
}
.benefits-card {   
    width: 25%;
    @extend .flex-column-center;
    @media (max-width: $mn) {
        width: 50%;
    }
    @media (max-width: $xx) {
        width: 100%;
    }
    &:nth-child(4) {
            
            
        .benefits-card__box-icon {
            margin-bottom: 10px;
            
        }
       
        
        
    }
    
    
}

.benefits-card__box-icon {
    background-color: #24558d;
    border-radius: 20px;
    width: 208px;
    height: 191px;
    @extend .flex-center;
    margin-bottom: 40px;
   @media (max-width: $x) {
    margin-bottom: 20px;
   }
}

.benefits-card__icon {
}
.benefits-card__text {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #5e5c5d;
}