/* Reset and base styles  */
*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    height: 100%; 
    // scroll-behavior: smooth;
}

body {       
    margin: 0;
    font-family: "CenturyGothic", sans-serif;
    color: #000;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    @media (max-width: 424px) {
        font-size: 16px;
    }
}

.body-sticky {
    display: flex; 
    flex-direction: column; 
    justify-content: space-between;
    height: 100%;
}

.footer-sticky {
    margin-top: auto;
}

body > * {
    flex-shrink: 0; 
}
body.noscroll {
    overflow: hidden;
}
#overlay {
    display: none;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.35);
    transition: 0.5s;
    position: fixed;
    &.active {
        display: block;
    }
}

.container {
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}
.none {
    display: none !important;
}
.list-scrollbar  {  
    overflow-y: auto;
    scrollbar-color: rgba(153, 166, 183, 0.5) #fff;
    scrollbar-width: thin; 
}
.list-scrollbar::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    background-color: #FFFFFF;  
}
.list-scrollbar::-webkit-scrollbar-track {
    background-color: #fff;
    border-radius: 4px;
}
.list-scrollbar::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background: rgba(153, 166, 183, 0.5);
}

.anchor {
    padding-top: 100px;
    margin-top: -100px;
    -webkit-background-clip: content-box;
    background-clip: content-box;
}


.header-bg {    
    @extend .bg;
}
.catalog-bg {
    @extend .bg;
    background-position: top;
}
.sertificate-bg {
    position: relative;
    background-image: url('./../img/bg.png');
        @extend .bg;
        background-position: top;
 
    
}
.bg-body {
    position: relative;
    &::before {
        position: absolute;
        content: '';
        background-image: linear-gradient(-90.0deg, rgba(41,192,236,0.45) 0%, rgba(0,71,200,0.45) 100%);
        width: 100%;
        height: 100%;
        opacity: .1;
    }
    
    
    
}

.btn {
    position: relative;
    display: block;
    text-align: center;
    font-family: var(--font-m);
    font-size: 18px;    
    font-weight: 800;
    color: #ffffff;
    background-image: linear-gradient( 45deg, rgb(124,172,236) 0%, rgb(54,126,239) 0%, rgb(190,171,167) 0%, rgb(95,143,198) 0%, rgb(36,85,141) 100%); 
    border-radius: 3px;
    padding: 23px 65px;
    transition: background-image 0.3s ease-in;
    @media (max-width: $x) {
        padding: 15px 45px;
    }
    &:hover {
        background-image: linear-gradient(90.0deg, #5f8fc6 0%, #0497cd 100%);
    }
    
}
.btn--small {
   padding-top: 20px;padding-bottom: 20px; 
}
.link-underline {
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s ease-in;
    &:hover {
        border-bottom: 1px solid currentColor;
    }
}

.controls {
    position: absolute;
    z-index: 1;
}
.swiper-btn {
    position: absolute;
    cursor: pointer;
    width: 62px;
    height: 62px;
    @media (max-width: $xs) {
        width: 55px;
        height: 55px;
    } 
    &::before {
        position: absolute;
        content: '';        
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        width: 100%;
        height: 100%;
        transition: background-image 0.3s ease-in;
        @media (max-width: $xs) {
            background-size: 40px;
        }
    }   
}
.prev {
    &::before {     
        background-image: url('./../img/icons/arrow_prev_white.png');
    }
   
    &:hover {
        &::before {     
            background-image: url('./../img/icons/arrow_prev_blue.png');
        }
        
    }
}
.next {
    &::before {   
        background-image: url('./../img/icons/arrow_next_white.png');
    }
    &:hover {
        &::before {     
            background-image: url('./../img/icons/arrow_next_blue.png');
        }
        
    }
}