.section-product {
    position: relative;
    padding: 0px 0 22px;
}

.section-product__header {
    max-width: 1150px;
    margin-right: auto;
    margin-left: auto;
}
.section-product__title {
    margin-bottom: 40px;
}

.section-product__desc {
    margin-bottom: 45px;
}
.section-product__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 580px 552px;
    @media (max-width: $l) {
        grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: $mn) {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 500px);
    }
    @media (max-width: $m) {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(6, 400px);
    }
}
.card-link {
    position: relative;
    display: flex;
    @extend .bg;
    transition:  0.3s ease-in;
    &:nth-child(1) {
        grid-column: 1/3;
        @media (max-width: $l) {
            grid-column: 1/2;
        }
        @media (max-width: $m) {
            
        }
    }
    &:nth-child(2) {
        grid-column: 3/5;
        @media (max-width: $l) {
            grid-column: 2/3;
        }
        @media (max-width: $m) {
            grid-column: 1/2;
        }
    }
    &:hover {
        .card-link__content  {
            min-height: 100%;
            // height: 100%;
            &::before {
                position: absolute;
                content: '';
                background-image: url('./../img/icons/arrow_douwn.png');
                
            }
        }
        .card-link__title {
            margin-bottom: 30px;
            @media (max-width: $xx) {
                margin-bottom: 15px;
            }
        }
        .card-link__content  {
            padding-top: 170px;
            @media (max-width: $xx) {
                padding-top: 120px;
            }
        }
         .card-link__btn {
            display: block;
        }
        .card-link__desc {
            opacity: 1;
            visibility: visible;
            height: 110px;
            display: block;
            @media (max-width: $xx) {
                height: 90px;
            }
        }
    }
}
.card-link__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: var(--font-m);    
    color: #fefefe;
    position: relative;
    margin-top: auto;
    width: 100%;
    // height: auto;
    min-height: 240px;
    // min-height: 100%;
    transition:  0.1s ease-in;
    background: rgba(0, 0, 0, .5);
    padding-top: 130px;
    padding-left: 10px;
    padding-right: 10px;
    @media (max-width: $x) {
        min-height: 200px;
        padding-top: 100px;
    }
    &::before {
        position: absolute;
        content: '';
        background-image: url('./../img/icons/arrow_up.png');
        background-repeat: no-repeat;        
        background-size: auto;
        width: 62px;
        height: 62px;
        top: 50px;
        left: 50%;
        transform: translateX(-50%);
        @media (max-width: $xx) {
            top: 30px;
        }
    }
}
.card-link__btn {
   
    display: none;
    margin-bottom: 35px;
}
.btn {
}
.card-link__title {
    text-align: center;
    font-size: 30px;
    line-height: 1;    
    font-weight: 600;
    @media (max-width: $x) {
        font-size: 20px;
    }
}
.card-link__desc {
    font-size: 18px;
    font-weight: 400;
    max-width: 510px; 
    transition:  0.1s ease-in;   
    opacity: 0;
    height: 0;
    visibility: hidden;
    overflow: hidden;
    // display: none;
    @media (max-width: $x) {
        max-width: 100%;
    }
}