.circle {
    position: absolute;
}
.cilrcle-1 {
    top: 38px;
    left: 0;
    width: 173px;
    height: 305px;
    @media (max-width: $mn) {
        width: 100px;
        height: 210px;
    }
    @media (max-width: $x) {
        top: -15px;

    }
}
.cilrcle-2 {
    top: 44%;
    left: 14%;
    width: 405px;
    height: 400px;
    @media (max-width: $mn) {
        top: 45%;
        left: 14%;
        width: 270px;
        height: 300px;
    }
    @media (max-width: $x) {
        top: 36%;
        left: 20%;
        width: 215px;
        height: 230px;
    
    }
}
.cilrcle-3 {
    top: 47%;
    right: 0;
    width: 237px;
    height: 310px;
    @media (max-width: $mn) {
        top: 24%;
        right: 0;
        width: 150px;
        height: 240px;
    }
    @media (max-width: $xx) {
        display: none;
    }
}
.cilrcle-4 {
    top: 48%;
    left: 0;
    width: 255px;
    height: 310px;
    opacity: 0;
    animation: circle-3 2s linear 3s infinite alternate;
}
.cilrcle-5 {
    top: 26%;
    right: 0;
    width: 480px;
    height: 505px;
    opacity: 0;
    animation: circle-3 2s linear 3s infinite alternate;
}
.cilrcle-6 {
    top: 15%;
    left: 0;
    width: 180px;
    height: 390px;
    @media (max-width: $mn) {
        width: 120px;
        height: 310px;
    }
    @media (max-width: $x) {
        top: -15px;

    }
}
.cilrcle-7 {
    top: 5%;
    left: 10px;
    width: 315px;
    height: 310px; 
    @media (max-width: $m) {
        display: none;
    }  
    @media (max-width: $xs) {
        width: 300px;
        height: 300px;
    }
}
.cilrcle-8 {
    top: 26%;
    right: 0;
    width: 285px;
    height: 515px;
    
    @media (max-width: $xx) {
        top: 56%;
    }
}
.cilrcle-9 {
    top: 40%;
    left: 4%;
    width: 500px;
    height: 520px;
    
    @media (max-width: $m) {
        width: 250px;
        height: 250px;
        top: 0%;
    }
}
.circle-img {
    position: absolute;
}
.animation-1 {
    // opacity: 0;
    animation: circle-1 2s linear 3s infinite alternate;
}
.animation-2 {
    opacity: 0;
    animation: circle-2 1.5s linear 1s infinite alternate;
}
.animation-3 {
    opacity: 0;
    animation: circle-3 2s linear 3s infinite alternate;
}
.animation-4 {
    animation: circle-4 10s linear 3s infinite ;
}
@keyframes circle-1 {
    0% {
        // opacity: 0;
        transform: scale(1);
    }
    100% {
        // opacity: 1;
        transform: scale(1.1);
    }
}
@keyframes circle-2 {
    0% {
        opacity: 0;
        // transform: rotate(0);
    }
    100% {
        opacity: 1;
        // transform: rotate(180deg);
    }
}
@keyframes circle-3 {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes circle-4 {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);;
    }
}