.header {
    padding: 0px 0 6px;
    @media (max-width: $l) {
        padding: 5px 0 5px;
    }
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: $l) {
        flex-direction: column;
    }
}
.logo {
    flex: 0 0 auto;
    @media (max-width: $l) {
        margin-bottom: 20px;
    }
    @media (max-width: $x) {
        margin-bottom: 10px;
        width: 200px;
    }
}
.header__block {
    display: flex;
    align-items: center;
    @media (max-width: $l) {
        width: 100%;
        justify-content: space-between;
    }
    @media (max-width: $xx) {
        flex-direction: column;
    }
}
.header__block-center {   
    margin-right: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 1240px) {
        margin-right: 10px;
    }
    @media (max-width: $mn) {
        width: 100%;
        margin-right: 40px;
    }
    @media (max-width: $m) {
        margin-right: 15px;
    }
    @media (max-width: $xx) {
        margin-bottom: 10px;
        width: 72%;
        margin-right: 0;
    }
    @media (max-width: $x) {
        width: 100%;
    }
}
.header__box-map {
    margin-top: 20px;
    margin-right: 67px;
    font-family: var(--font-m);
    font-size: 16px;   
    font-weight: 600;
    color: #ffffff;
    @media (max-width: $mn) {
        margin-top: 0px;
        margin-right: 0px;
    }
    span {
        display: block;
        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }
}
.header__tel {
    font-family: var(--font-m);
    font-size: 16px;   
    font-weight: 600;
    color: #ffffff;
    margin-right: 50px;
    @media (max-width: $mn) {
        display: none;
    }
}
.header__contacts {
    display: flex;
    align-items: center;
    
}
.contacts {
}
.contacts__icon {
    cursor: pointer;
    
    transition: transform 0.3s ease-in;
    &:hover {
        transform: scale(1.1);
    }
}
.contacts__tel {
    
}
.contacts__email {
    width: 41px;
    height: 41px;
}
.contacts__icon--glass {
    width: 24px;
    height: 24px;
    margin-right: 40px;
    @media (max-width: 1240px) {
        margin-right: 10px;
    }
    @media (max-width: $mn) {
        margin-right: 0;
    }
}
.contacts__icon--tel {
    width: 41px;
    height: 41px;
    margin-right: 10px;
    @media (max-width: $mn) {
        display: none;
    }
}
.contacts__icon--email {
    width: 41px;
    height: 41px;  
    @media (max-width: $mn) {
        display: none;
    }  
}
.header__right {
    flex: 0 0 auto;
}
.header__btn {
    
}
