.nav-icon {
    display: none;
    width: 50px;
    height: 50px;
    background: #ffffff;
    // box-shadow: 0px 0px 58px rgba(255, 255, 255, 0.65);
    border-radius: 12px;
    padding: 15px 10px;
    align-items: center;
    @media (max-width: $m) {
        display: flex;
        z-index: 4;  
        position: fixed;
        top: 24px;
        right: 20px;    
    }
	@media (max-width: $xs) {
		top: 5px;
		right: 6px;
	}
}

.nav-icon span {
    position: relative;
    width: 100%;
    height: 5px;
    background: var(--color-b);
    border-radius: 25%;
    &::before,
    &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 5px;
        background: var(--color-b);
        border-radius: 25%;
        transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
      }
      &::before {
        top: -10px;
      }
      &::after {
        top: 10px;
      }
}
// при нажатии на бургер
.nav-icon.active span {
    background: transparent;
    &::before,
    &::after {
      background: var(--color-b);
      top: 0px;
      transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
    }
    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
}
