.section-catalog {
    position: relative;
    padding-bottom: 60px;
    @media (max-width: $x) {
        padding-bottom: 30px;
    }
}
.section-catalog__title {
    margin-bottom: 37px;
}
.section-catalog__inner-row {
    margin-bottom: 20px;

    .section-catalog__row {
        &:last-child {
            margin-bottom: 0;
        }
    }
}
.section-catalog__row {
    display: grid;
    grid-template-columns: minmax(auto, 437px) minmax(auto, 472px) 361px;
    box-shadow: 0px 0px 9px 4px rgba(127, 132, 137, 0.31);
    @media (max-width: $l) {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 350px;
    }
    @media (max-width: $m) {
        grid-template-columns: 1fr;
        grid-template-rows: unset;
    }
    margin-bottom: 30px;
        @media (max-width: $x) {
            margin-bottom: 20px;
        }
   
    &:hover {
        .section-catalog-card {
            &:nth-child(1) {
                background-image: linear-gradient( 0deg, rgb(95,143,198) 0%, rgb(4,151,205) 0%, rgb(107,69,233) 0%, rgb(190,171,167) 0%, rgb(116,154,250) 100%);
            }
        }
        .section-catalog-card__btn {
            background-image: linear-gradient(90deg, #5f8fc6 0%, #0497cd 100%);
        }
    }
}
.section-catalog-card {
    &:nth-child(1) {
        position: relative;
        padding: 20px;
        background: #fff;
        transition: 0.3s ease-in;
        @media (max-width: $x) {
            padding: 10px;
        }
        &::before {
            position: absolute;
            content: "";
            background-image: url("./../img/icons/increase_icon.png");
            background-repeat: no-repeat;
            background-position: 0px 0px;
            background-size: auto;
            width: 55px;
            height: 55px;
            top: 25px;
            right: 25px;
            cursor: pointer;
        }
    }
    &:nth-child(2) {
        position: relative;
        background-color: #f8f8fa;
        box-shadow: 0px 0px 9px 4px rgba(127, 132, 137, 0.31);
        padding: 30px 44px 36px 48px;
        transition: 0.3s ease-in;
        @media (max-width: $xx) {
            padding: 30px;
        }
    }
    &:nth-child(3) {
        // background-image: linear-gradient(0deg, #a6c1df 0%, #74c5e3 100%);
        background-image: linear-gradient( 0deg, rgb(95,143,198) 0%, rgb(4,151,205) 0%, rgb(107,69,233) 0%, rgb(190,171,167) 0%, rgb(116,154,250) 100%);
        padding: 65px 34px 65px 25px;
        @media (max-width: $l) {
            grid-column: 1/3;
        }
        @media (max-width: $m) {
            grid-column: unset;
        }
        @media (max-width: $x) {
            padding: 30px;
        }
    }
}
.section-catalog-card__holder-image {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #e8e9ec;
    border: 1px solid rgba(60, 89, 131, 0.23);
}
.section-catalog-card__img {
}
.section-catalog-card__name {
    font-size: 25px;
    font-weight: 700;
    color: var(--color-b);
    border-bottom: 1px solid rgba(60, 89, 131, 0.23);
    padding-bottom: 5px;
    margin-bottom: 20px;
    @media (max-width: $xx) {
        font-size: 20px;
    }
}
.section-catalog-card__list {
    padding-left: 35px;
}
.card-info {
}
.card-info__item {
    position: relative;
    display: table;
    border-collapse: collapse;
    width: 100%;
    &::before {
        position: absolute;
        content: "";

        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        width: 20px;
        height: 20px;
        top: 50%;
        left: -35px;
        transform: translateY(-50%);
    }
    &:nth-child(1) {
        &::before {
            background-image: url("./../img/icons/i_1.png");
        }
    }
    &:nth-child(2) {
        &::before {
            background-image: url("./../img/icons/i_2.png");
        }
    }
    &:nth-child(3) {
        &::before {
            background-image: url("./../img/icons/i_3.png");
        }
    }
    &:nth-child(4) {
        &::before {
            background-image: url("./../img/icons/i_4.png");
        }
    }
    &:nth-child(5) {
        &::before {
            background-image: url("./../img/icons/i_5.png");
        }
    }
}
.card-info__text {
    overflow: hidden;
    position: relative;
    display: table-cell;
    width: 78%;
    font-size: 17.3px;
    line-height: 34px;
    font-weight: 400;
    color: #3a3a3a;
    @media (max-width: $xx) {
        width: 90%;
    }
    &:after {
        content: "";
        position: absolute;
        bottom: 10px;
        margin-left: 5px;
        width: 100%;
        height: 0;
        border-bottom: 1px dashed rgba(60, 89, 131, 0.23);
    }
    span {
        // width: 230px;
        // display: inline-table;
    }
}
.card-info__num {
    display: table-cell;
    padding-left: 5px;
    white-space: nowrap;
    vertical-align: bottom;
}

.section-catalog-card__excerpt {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #000000;
    padding-top: 30px;
    margin-bottom: 60px;
    border-top: 1px solid rgba(60, 89, 131, 0.23);
    @media (max-width: $x) {
        margin-bottom: 30px;
    }
}
.section-catalog-card__btn {
}
.btn {
}


.section-catalog__box-btn {
    @extend .flex-center;
    
}
.section-catalog__link {   
    width: max-content;    
    padding: 23px 50px;
@media (max-width: $x) {
    padding: 23px 14px;
}
}
