.news {
    position: relative;
    padding-bottom: 55px;
}

.news__title {
    margin-bottom: 70px;
    @media (max-width: $xx) {
        margin-bottom: 40px;
    }
}

.news__container {
    position: relative;
    max-width: 1170px;   
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 25px;
    gap: 10px 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    @media (max-width: $mn) {
        grid-template-columns: 1fr;
        grid-template-rows: 600px 307px 307px;
    }
    @media (max-width: $m) {
        grid-template-rows: 1fr minmax(330px, auto);
        // grid-template-rows: 600px 331px 331px;
    }
}
.news-card {
    display: block;
    background-image: linear-gradient( 90deg, rgb(95,143,198) 0%, rgb(4,151,205) 0%, rgb(107,69,233) 0%, rgb(190,171,167) 0%, rgb(116,154,250) 100%);
    &:nth-child(1){}
    &:nth-child(2){
        grid-row: 1/3;
        @media (max-width: $mn) {
            grid-row: 1/2;
        }
        .news-card__body {
            height: 570px;
            padding: 74px 24px 10px 40px;
            @media (max-width: $l) {
                height: 600px;
            }
            @media (max-width: $mn) {
                height: 523px;
            }
            @media (max-width: $m) {
                height: 500px;
            }
            @media (max-width: $xx) {
                padding: 20px;
                height: unset;
            }
            @media (max-width: $xs) {
                padding: 20px 20px 30px; 
            }
        }
    }
    &:nth-child(3){
        grid-column: 1/2;
        grid-row: 2/3;
        @media (max-width: $mn) {
            grid-row: 3/4;
        }
    }

    &:hover {
        .news-card__body {
            
            color: #fff;
           p {
                color: #fff;
                position: relative;
           }
            &::before {
                opacity: 1;
            }
        }
        .news-card__link  {
            background-image: linear-gradient( 90deg, rgb(95,143,198) 0%, rgb(4,151,205) 0%, rgb(107,69,233) 0%, rgb(190,171,167) 0%, rgb(116,154,250) 100%);
        }
    }
}
.news-card__header {
    padding: 15px 40px 13px 40px;
    border-bottom: 1px solid #fff;
    @media (max-width: $m) {
        padding: 15px;
    }
}
.news-card__title {
    font-size: 22px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
    @media (max-width: $xx) {
        font-size: 20px;
    }
}
.news-card__body {    
    position: relative;
    padding: 32px 24px 10px 40px;
    transition:  0.3s ease-in;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: #000000;
    @media (max-width: $x) {
        padding: 20px;
    }
    @media (max-width: $xs) {
        padding: 20px 20px 30px; 
    }
    p {
        margin-bottom: 20px;

    }
    &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.53);
        background-image: url('./../img/bg_news_hover.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0;
        transition: opacity 0.3s ease-in;
    }
   
}
.news-card__body--notmargin {
    p {
        margin-bottom: 0;
    }
}

.news-card__footer {
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 30px;
}
.news-card__link {
   padding: 20px 74px;
    margin-right: 30px;
    @media (max-width: $l) {
        padding: 20px 50px;
    }
    @media (max-width: $xx) {
        padding: 15px 30px;
        margin-right: 15px;
    }
}
.btn {
}
.news-card__icon {
    flex: 0 0 auto;
    margin-right: 55px;
    @media (max-width: $l) {
        margin-right: 30px;
    }
    @media (max-width: $xx) {
        margin-right: 15px;        
        width: 40px;
        height: 40px;
    }
}
.news-card__date {
    text-decoration: underline;
    @media (max-width: $x) {
        font-size: 12px;
    }
    @media (max-width: $xs) {
        position: absolute;
        bottom: -25px;
    }
}

.news__box-btn {
    @extend .flex-center;
}
