.hero {
    position: relative;
    padding: 212px 0 133px;
    @media (max-width: $xx) {
        padding: 120px 0 80px;
    }
    @media (max-width: $x) {
        padding: 80px 0 50px;
    }
}

.hero__content {
    position: relative;
    max-width: 730px;
    margin: 0 auto;
    display: grid;
}
.hero__title {
    margin-bottom: 34px;
    @media (max-width: $x) {
        margin-bottom: 20px;
    }
}
.hero__subtitle {
    margin-bottom: 265px;
    @media (max-width: $xx) {
        margin-bottom: 80px;
    }
    @media (max-width: $x) {
        margin-bottom: 50px;
    }
}
.hero__link-bottom {
    justify-self: center;
    transition: transform 0.3s ease-in;
    &:hover {
        transform: scale(1.1);
    }
}


