.about {
    position: relative;
    padding-bottom: 60px;
    @media (max-width: $x) {
        padding-bottom: 45px;
    }
}

.about__title {
    margin-bottom: 43px;
}

.about__list {
    position: relative;
    column-count: 2;
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 23px;
    @media (max-width: $m) {
        column-count: 1;
        max-width: 100%;

    }
}
.about__item {  
    line-height: 36px;   
    color: #58585c;
    margin-bottom: 35px;
    max-width: 505px;
    @media (max-width: $m) {
        max-width: 100%;
        margin-bottom: 20px;
        line-height: 1.3;   
    }
}

.about__box-btn {
    @extend .flex-center;
}
.about__btn {   
    
    padding: 20px 87px;
@media (max-width: $xs) {
    padding: 20px 70px;
}
}
